  src: url("../fonts/raleway-extra-bold.woff2") format("woff2"), url("../fonts/raleway-extra-bold.woff") format("woff");
}
*,
*::before,
*::after {
  box-sizing: border-box;
}

html,
body {
  margin: 0;
  padding: 0;
  min-height: 100vh;
}

html {
  font-style: normal;
  font-weight: 400;
  font-size: 20px;
  line-height: 1.2;
  font-family: "Lato", "Arial", sans-serif;
  color: #000000;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-rendering: optimizeLegibility;
}

body {
  width: 100%;
  height: 100%;
  background-color: #ffffff;
}

a {
  text-decoration: none;
  color: #ffffff;
}

img,
video {
  display: block;
  max-width: 100%;
  height: auto;
}

textarea {
  resize: none;
}

input:-webkit-autofill {
  box-shadow: inset 0 0 0 1000px rgba(255, 255, 255, 0.85);
  -webkit-text-fill-color: #272733;
  transition: background-color 600000s 0s, color 600000s 0s;
}
input:-webkit-autofill:focus {
  transition: background-color 600000s 0s, color 600000s 0s;
}

input,
textarea {
  border-radius: 0;
}
input::placeholder,
textarea::placeholder {
  opacity: 1;
}
input:invalid,
textarea:invalid {
  box-shadow: none;
}

select {
  border-radius: 0;
}

input::-ms-clear, input::-ms-reveal {
  display: none;
}

input[type=search]::-webkit-search-decoration,
input[type=search]::-webkit-search-cancel-button,
input[type=search]::-webkit-search-results-button,
input[type=search]::-webkit-search-results-decoration {
  appearance: none;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  margin: 0;
  appearance: none;
}

input[type=number] {
  appearance: textfield;
}

select,
textarea,
input:matches([type=email],
[type=number],
[type=password],
[type=search],
[type=tel],
[type=text],
[type=url]) {
  appearance: none;
}

button,
[type=button],
[type=reset],
[type=submit] {
  appearance: none;
}

.wrapper {
  position: relative;
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  /* stylelint-disable-next-line */
  min-height: calc(100 * var(--vh, 1vh));
}
.wrapper .header,
.wrapper .footer {
  flex-shrink: 0;
}
.wrapper main {
  flex-grow: 1;
}

.rouble {
  /* stylelint-disable */
  font-weight: 400 !important;
  /* stylelint-enable */
  font-family: "Rouble", "Arial", sans-serif;
  /* stylelint-disable */
  text-transform: lowercase !important;
  /* stylelint-enable */
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  white-space: nowrap;
  border: 0;
  clip: rect(0 0 0 0);
  clip-path: inset(100%);
}

.scroll-lock-ios {
  position: fixed;
  overflow: hidden;
}

.scroll-lock {
  overflow: hidden;
}

.no-scrollbar {
  -ms-overflow-style: none;
  scrollbar-width: none;
}
.no-scrollbar::-webkit-scrollbar {
  display: none;
}

.no-transition {
  /* stylelint-disable-next-line */
  transition: none !important;
}

.js-focus-visible :focus:not(.focus-visible) {
  outline: none;
}

.container {
  width: 100%;
  max-width: 1250px;
  margin: 0 auto;
  padding: 0 40px;
}
@media (max-width: 1023px) {
  .container {
    padding: 0 20px;
  }
}
@media (max-width: 767px) {
  .container {
    padding: 0 10px;
  }
}

.header {
  padding: 33.5px 0 31.5px 0;
  background-color: rgba(16, 16, 29, 0.9);
  transition: transform 0.3s ease;
  z-index: 30;
}
.header--sticky {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
}
.header__wrapper {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
}
@media (max-width: 767px) {
  .header__wrapper {
    align-items: center;
  }
}
.header__nav-toggle {
  display: none;
}
@media (max-width: 767px) {
  .header__nav-toggle {
    display: flex;
    z-index: 30;
  }
}
.header__logo {
  margin-right: 60px;
}
@media (max-width: 1023px) {
  .header__logo {
    margin-right: 30px;
  }
}
@media (max-width: 767px) {
  .header__logo {
    z-index: 30;
  }
}
@media (max-width: 767px) {
  .header__nav {
    visibility: hidden;
    pointer-events: none;
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    padding-top: 100px;
  }
}
@media (max-width: 767px) {
  .header__nav .main-nav__wrapper {
    opacity: 0;
  }
}
@media (max-width: 767px) {
  .header__nav .main-nav__item {
    transform: translateX(-100%);
  }
}
.header.is-hidden {
  transform: translateY(-100%);
}
@media (max-width: 767px) {
  .header.is-open .header__nav {
    visibility: visible;
    pointer-events: all;
  }
}
@media (max-width: 767px) {
  .header.is-open .header__nav .main-nav__wrapper {
    opacity: 1;
  }
}
@media (max-width: 767px) {
  .header.is-open .header__nav .main-nav__item {
    transform: translateX(0);
  }
}

.main-nav {
  font-weight: 600;
  text-transform: uppercase;
  display: flex;
  flex-direction: column;
}
@media (max-width: 767px) {
  .main-nav__wrapper {
    padding: 0 20px 50px 20px;
    background-color: rgba(16, 16, 29, 0.9);
    flex-grow: 1;
    transition: opacity 0.3s ease;
    display: flex;
    flex-direction: column;
    max-height: calc(100 * var(--vh, 1vh) - 100px);
  }
}
.main-nav__list {
  display: flex;
  justify-content: flex-end;
  flex-wrap: wrap;
  margin: 0 0 0 -60px;
  padding: 0;
  list-style: none;
}
@media (max-width: 1023px) {
  .main-nav__list {
    margin-left: -30px;
  }
}
@media (max-width: 767px) {
  .main-nav__list {
    flex-direction: column;
    align-items: flex-start;
    justify-content: flex-start;
    margin: 0;
    overflow-x: hidden;
    flex-wrap: nowrap;
  }
}
.main-nav__item {
  display: flex;
  margin-left: 60px;
  transition: transform 0.3s ease;
}
@media (max-width: 1023px) {
  .main-nav__item {
    margin-left: 30px;
  }
}
@media (max-width: 767px) {
  .main-nav__item {
    margin: 0;
    width: 100%;
  }
}
.main-nav__link {
  padding: 9.71px 0;
}
@media (max-width: 767px) {
  .main-nav__link {
    width: 100%;
  }
}

.footer {
  padding: 30px 0;
  background-color: #3d4050;
}
.footer__wrapper {
  position: relative;
  display: grid;
  grid-template-columns: 0.8fr 1fr 0.8fr;
  gap: 19px 38px;
}
.footer__wrapper::before, .footer__wrapper::after {
  position: absolute;
  content: "";
  top: 0;
  bottom: 0;
  width: 1px;
  background-color: #c3c3c3;
}
@media (max-width: 767px) {
  .footer__wrapper::before, .footer__wrapper::after {
    display: none;
  }
}
.footer__wrapper::before {
  left: 30%;
}
.footer__wrapper::after {
  right: 30%;
}
@media (max-width: 767px) {
  .footer__wrapper {
    gap: 38px;
    grid-template-columns: 1fr;
  }
}
.footer__copyright {
  color: #ffffff;
  font-size: 13px;
  line-height: 1.2;
  margin: 0;
  display: flex;
  grid-column: 1/2;
  grid-row: 1/2;
}
@media (max-width: 767px) {
  .footer__copyright {
    grid-column: auto;
    grid-row: auto;
