.burger {
  width: 35px;
  height: 35px;
  padding: 0;
  border: none;
  border-radius: 3px;
  background-color: #43abf0;
  cursor: pointer;
  position: relative;
  flex-shrink: 0;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  transition: all 0.3s ease;
  user-select: none;
}
@media (hover: hover) {
  .burger:hover:not(.focus-visible) {
    background-color: #ffffff;
    box-shadow: inset 0 0 0 1px #43abf0;
  }
  .burger:hover:not(.focus-visible) > * {
    background-color: #43abf0;
  }
}
.burger.focus-visible:focus {
  background-color: #ffffff;
  box-shadow: inset 0 0 0 1px #43abf0;
}
.burger.focus-visible:focus > * {
  background-color: #43abf0;
}
.burger * {
  background-color: #ffffff;
  width: 20px;
  height: 2px;
  border-radius: 5px;
  transition: all 0.3s ease;
  user-select: none;
  pointer-events: none;
}
.burger__middle-line {
  margin: 5px 0;
}
.burger[aria-pressed=true] * {
  position: absolute;
}
.burger[aria-pressed=true] .burger__middle-line {
  width: 0;
  height: 0;
}
.burger[aria-pressed=true] .burger__top-line {
  top: calc(50% - 1px);
  transform: rotate(-45deg);
  width: 35px;
  left: 0;
}
.burger[aria-pressed=true] .burger__bottom-line {
  bottom: calc(50% - 1px);
  transform: rotate(45deg);
  width: 35px;
  right: 0;
}

